import React from "react";

const Picture = (props) => {
  const { id, title, url, owner } = props.pictureData;
  return (
    <article
      className="card text-white bg-dark mb-3"
      style={{ maxWidth: "20rem" }}
    >
      <img src={url} alt="Picture" />
      <h1 className="card-header" style={{ textAlign: "center" }}>
        {title}
      </h1>
      <p card-text style={{ marginTop: "0.5rem", textAlign: "center" }}>
        ðŸ“·: {owner}
      </p>
    </article>
  );
};

export default Picture;
