import React from "react";
import { Pictures } from "../NumberTwo/pictures";
import Picture from "../NumberTwo/Picture";
import "../NumberTwo/bootstrap.css";

function pictureList() {
  return (
    <section className="picture">
      {Pictures.map((picture) => {
        return <Picture id={picture.id} pictureData={picture}></Picture>;
      })}
    </section>
  );
}

export default pictureList;
